import axios from 'axios'
import React, { Component } from 'react'

export default class 
 extends Component {
  state ={
    articles : [] ,
  }

  getnews = async ()=> {
    let {data} = await axios.get(`https://mockend.com/HosamZewain/test/posts`);
    this.setState({articles : data});
    console.log(data);
    
    
     
  }
  
  
  componentDidMount(){
    this.getnews();

}



  render() {
    return (
      <div className='row mt-5   '>
        
      

        { this.state.articles.length > 0 ?
          this.state.articles.map((article ,index)=> 
          <div key={index} className='col-md-3'>
            <div className='article'>
              <img src={article.image} className='w-100' alt="" />
              <h4 className='fw-bold'>{article.title}</h4>
              <p>{article.body}</p>
            </div>
          </div>
          ) : <h2 className='text-center'><i className='fas fa-spinner fa-spin'></i></h2>
        }

      </div>
    )
  }
}
