import ReactDOM from 'react-dom/client';
import { BrowserRouter } from "react-router-dom";
import App from './App';
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import { Provider } from 'react-redux';
import { createStore } from 'redux';

const root = ReactDOM.createRoot(document.getElementById('root'));


let count = 0;

//Action Creator
export function increase() {
    return {
        type: 'INC'
    }
}
//Action Creator
export function decrease() {
    return {
        type: 'DEC'
    }
}
// Action Creator
export function incByVal (val){
    return{
        type : 'incVal',
        payload : val 
    }
}

//Reducer
function counter(prevState = {count}, action) {

    switch (action.type) {
        case 'INC':

            return {...prevState , count : prevState.count +1};
        case 'incVal':

            return {...prevState , count : prevState.count + action.payload};

        case 'DEC':

            if (prevState.count <= 0)
                return prevState;
            else
                return {...prevState,count :prevState.count - 1};

        default:
            return prevState;
    }



}

//Store
let enhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
let store = createStore(counter , enhancer());
root.render(



    <BrowserRouter>
        <Provider store={store}>

            <App />
        </Provider>
    </BrowserRouter>




);