import React, { useState } from 'react'
import axios from 'axios';
import { useForm } from "react-hook-form";
import { useNavigate } from 'react-router-dom';

export default function SignUp() {

    const { register, handleSubmit, formState: { errors },control,watch} = useForm();

    const [error, setError] = useState('');

    const [isLoading, setIsLoading] = useState(false);

    const [user, setUser] = useState({
        first_name  : '',
        last_name : '',
        age : 0 ,
        email : '',
        password : ''
    })

    let navigate = useNavigate();


    function getUser (e){
        //deep copy 
        let myUser = {...user};

        //change
        myUser[e.target.name] = e.target.value ;

        //update
        setUser(myUser);

        console.log(myUser[e.target.name]);
        console.log(myUser);
    }

   async function submitRegister(e){
        e.preventDefault();
        setIsLoading(true);
        let {data} = await axios.post(`https://route-egypt-api.herokuapp.com/signup`,user);
        console.log(data);
        
        if (data.message === 'success' ){
            // Navigate to Login
            navigate('/login')

            console.log("response");
            setIsLoading(false);

        }else{
            setError(data.message);
            setIsLoading(false);

        }
        
    }



    return (
        <div >
            <h2 className='my-3 mx-auto d-flex align-items-center justify-content-center'>REGISTER NOW ...</h2>
            
            {error.length > 0 ? <div className="alert alert-danger text-center">{error}</div> : '' }
            
            <form onSubmit={submitRegister} className='mt-5 mx-5'>
                {/* Fisrt Name */}
                <label htmlFor="first_name">First Name :</label>
                <input onChange={getUser} type="text" name="first_name" id="first_name" className='form-control my-3'  />

                
                {/* Last Name */}
                <label htmlFor="last_name">Last Name :</label>
                <input onChange={getUser} type="text" name="last_name" id="last_name" className='form-control my-3'  />
                
                

                {/* Age */}
                <label htmlFor="age">Age :</label>
                <input onChange={getUser} type="number" name="age" id="age" className='form-control my-3'  />
                
                

                {/* Email */}
                <label htmlFor="email">Email :</label>
                <input onChange={getUser} type="email" name="email" id="email" className='form-control my-3'  />
                
                    
                {/* Password */}
                <label htmlFor="password">Password :</label>
                <input onChange={getUser} type="password" name="password" id="password" className='form-control my-3'  />
               
                <div className='d-flex align-items-center justify-content-center'>

                    <button type='submit' className='btn btn-outline-info mb-5'>
                        {isLoading ?<i className="fas fa-spinner fa-spin"></i> :'Register'}
                    </button>
                </div>
            </form>
        </div>
    )
}
