import React, { Component } from 'react'
import  { Outlet,Link } from 'react-router-dom'


export default class
    extends Component {
    render() {
        return (
            <div>
                <li>

                    <Link to='web'>Web Project</Link>
                </li>
                <li>

                    <Link to='mobile'>Mobile Project</Link>
                </li>
                <Outlet />
            </div>
        )
    }
}
