import React, { Component } from 'react';


export default class extends Component {
  render() {
    
    let { id, name, price, category, onSale ,count} = this.props.productInfo;

    return (
      <div className='col-md-3'>
        <div className='bg-light p-1 position-relative'>
          <h5>Name :{name}</h5>
          <h5>price :{price}</h5>
          <h5>category :{category}</h5>
          <h5>id :{id}</h5>
          <h5>id :{count}</h5>
          <button className='btn btn-outline-danger w-100 mt-2' onClick={()=>this.props.update(this.props.productInfo)}>increse count</button>
          <button className='btn btn-outline-danger w-100 mt-2' onClick={()=>this.props.delete(id)}>Delete</button>
          
          {
            onSale === 'true' ? <div className='sale bg-danger p-2 mx-2 text-white position-absolute top-0 end-0'> Sale</div> : ""
          }


        </div>
      </div>
    )
  }
}
