import React, { Component } from 'react';
import Child from '../child/Child';

class Parent extends Component {
    state = {
        products :[
            {id:0 , name:'toshiba' ,price : 1000 , category : "tv" , onSale : "true",count :5},
            {id:1 , name:'sony' ,price : 2000 , category : "tv" , onSale : "false",count :4},
            {id:2 , name:'apple' ,price : 3000 , category : "tv" , onSale : "true",count :8},
            {id:3 , name:'DELL' ,price : 4000 , category : "tv" , onSale : "false",count :7},
            {id:4 , name:'HP' ,price : 1500 , category : "tv" , onSale : "true",count :23},
            {id:5 , name:'sony' ,price : 1500 , category : "tv" , onSale : "false",count :23},
            {id:6 , name:'apple' ,price : 1500 , category : "tv" , onSale : "true",count :23},
            {id:7 , name:'HP' ,price : 1500 , category : "tv" , onSale : "false",count :23},
        ]
      }

      deleteProduct =(id)=> {
        //deep copy
        let products = [... this.state.products];
        //update
        products = products.filter((product)=>product.id != id);
        //render
        this.setState({products});
      }
      updateProduct = (product)=>{
        //deep copy
        let newProduct = [... this.state.products];
        let productIndex = newProduct.indexOf(product);
        //update
        newProduct[productIndex].count +=1 ;
        //render
        this.setState({products : newProduct})
      }
      
    render() {
        return (
            <div className='bg-secondary container py-3 m-1'>
                <div className='row g-1'>
                    {this.state.products.map((product)=> <Child key={product.id} update = {this.updateProduct} delete = {this.deleteProduct} productInfo={product} />)}
                </div>
            </div>
        );
    }
}

export default Parent;