import React, { Component } from 'react'

export default class About extends Component {
  state={
    count : 0 
  }
  changeCount = ()=>{
    // copy
    let myCount = this.count ;
    //change 
    myCount = Math.random();
    // update
    this.setState({count : myCount})
  }
  render() {
    return (
      <div>
        <h2 className='mb-5'>Class Components</h2>
        <h4 > Count : {this.state.count}</h4>
        <button className='btn btn-primary' onClick={()=>this.changeCount()}>change</button>

        
      </div>
    )
  }
}
