import React, { Component } from 'react';
import { Link } from 'react-router-dom'


class Navbar extends Component {
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-lg navbar-white bg-dark navbar-dark py-4">
                    <ul className="nav text-white bg-dark ">
                        <li className="nav-item">
                            <Link className="nav-link text-white" aria-current="page" to="home">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link text-white" to="about">About</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link text-white" to="parent">Parent</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link text-white" to="projects">Projects</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link text-white" to="comparison">Comparison</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link text-white" to="redux">Redux</Link>
                        </li>

                    </ul>
                    <ul className='nav ms-auto text-white bg-dark me-4 '>
                        <li className="nav-item">
                            <Link className="nav-link text-white" to="login">Login</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link text-white" to="signup">Signup</Link>
                        </li>
                        

                    </ul>
                </nav>
            </div>
        );
    }
}

export default Navbar;